
import XHeader from 'vux/src/components/x-header/index.vue'

import Collapse from "@/components/Collapse/Collapse";
import showMore from "@/components/ShowMore/showMore"; 
export default {
components: {
XHeader,
Collapse,
showMore
},
data() {
return {
info: {},
}
},
mounted() {
this.getDeathRecord();
},
methods: {

getDeathRecord() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "deathService",
"MSH.2": "getById",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
}
};



